
.tb-responsive > table > thead{
    background-color: #01477b;
}

.tb-responsive > table > thead > tr > th { 
    color: #fff;
    padding: 8px;
}

.tb-responsive > table > tbody > tr:nth-of-type(odd){
    background-color: #e6f4ff;
}

.tb-responsive > table > tbody > tr { 
   border: 1px solid transparent !important;
}

.tb-responsive > table > tbody > tr > td { 
    color: #212529;
    padding: 8px;
}