@font-face {
    font-family: 'DB Ozone X';
    src: local('DB Ozone X'), url('../fonts/DB Ozone X v3.2_0.ttf') format('truetype');
    font-size-adjust: 0.58;
}

html,
body,
body > #root,
#app,
#app > div {
    height: 100%;
    /* font-family: 'DB Ozone X';
    font-size: 110%; */
}

.cursor-pointer {
    cursor: pointer;
}

.bg-main {
    background-color: #1E73BE;
}

.bg-alt {
    background-color: #e6f4ff;
}

.shadow-b{
    box-shadow: 0 4px 8px rgba(0, 0, 0, .4);
}

.page-title{
    border-bottom: 3px solid #f5f5f5;
}

.text-title{
    color: #01477b;
}